<template>
  <div class="game">
     <div class='header'>
      <div class='header_wrapper'>
        <h1>{{ gameInfo.name }}</h1>
        <div class='header_info'>
          <span>Players: {{ gameInfo.minPlayers }} - {{ gameInfo.maxPlayers }}</span>
          <span>Playing time: {{ gameInfo.playingTime }}'</span>
        </div>
      </div>
    </div>
    <div class="arrow-down"></div>
    <div class='wrapper'>
      <div class="info">
        <p>{{ gameInfo.description | removeLineFeed | replaceQuotes | replaceDash | replaceDash2 | replaceSingleQuote | replaceEstLetters | replacePolishLetters | removeGermanText }}</p>
        <br>
        <a :href="url" target="_blank">Get more info @BGG</a>
      </div>
      <div class="picture">
        <img :src="gameInfo.image" alt="" height="400">
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'game',
  data() {
    return {
      gameInfo: {},
      url: null
    }
  },
  methods: {
    loadGame() {
      let id = this.$route.params.id
      let link = `https://bgg-json.azurewebsites.net/thing/${id}`
      axios.get(link)
        .then(response => {
         this.gameInfo = response.data;
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    createUrl() {
      let id = this.$route.params.id
      return this.url = `https://boardgamegeek.com/boardgame/${id}`
    }
  },
  filters: {
    removeLineFeed(value) {
      return value.replace(/&#10;/g, " ")
    },
    replaceQuotes(value) {
      return value.replace(/&quot;/g, '"')
    },
    replaceDash(value) {
      return value.replace(/&ndash;/g, " - ")
    },
    replaceDash2(value) {
      return value.replace(/&mdash;/g, " - ")
    },
    replaceSingleQuote(value) {
      return value.replace(/&rsquo;/g, "'")
    },
    replaceEstLetters(value) {
      return value.replace(/&auml;/g, "ä")
    },
    replacePolishLetters(value) {
      return value.replace(/&#196;&#133;/g, "ą")
    },
    removeGermanText(value) {
      return value.replace(/&#9;&#9;&#9;&#9;.*$/, "")
    }
  },
  beforeMount() {
    this.loadGame()
    this.createUrl()
  }
}
</script>

